
import { Typography } from "@material-ui/core";
import { Alert, Icon } from "@material-ui/core";
import { Grid, IconButton, Box } from "@material-ui/core";
import MoreVertIcon from '@material-ui/icons/MoreVert';
import humidity from "../images/humidity.svg";



const Item = ({ data, key, degUnit }) => (
    <Box key={key} class='bg-black text-white flex rounded-lg p-3 border-2 flex flex-col' style={{ minWidth: '21rem' }}>
    <Box id='header' class='flex flex-grow justify-between'>
        <Box id='location-date' class='w-6/12'>
            <Typography variant='p' className='font-medium' sx={{ color: 'error.main' }}>
                {data.date}
            </Typography>
            <Typography variant='p' className='block text-3xl font-medium'>
                {data.city}
            </Typography>
        </Box>

        <Box id='temp' class='w-5/12 text-center self-center'>
                <Typography variant='p' className='block text-4xl font-medium' sx={{ color: 'warning.light' }}>
                    {degUnit === 'C'? data.degC : data.degF}°{degUnit}</Typography>
        </Box>

        <Box id='delete-button' className='w-1/12'>
            <IconButton id="long-button" onClick={""}>
                <MoreVertIcon className='text-white hover:text-green-600' />
            </IconButton>
        </Box>
    </Box>

    <Box id='content' class='flex'>
        <Box id='weather-status' class='w-5/12 bg-red-90 flex flex-col items-center mt-3'>
            <img width='80' src={data.icon} alt='weather' />
            <Typography variant='p' className='text-2xl' sx={{ marginTop: '-15px' }}>{data.weatherStatus}</Typography>
        </Box>

        <Box className='w-7/12 flex flex-col space-y-2 mt-1'>
                <Typography variant='p' className=' mb-5 text-center '>
                    Feels like {degUnit === 'C'? data.degC : data.degF}°{degUnit}. {data.shortDescription}</Typography>
                <Typography><img width='25' className='inline-block' src={humidity} />Humidity : <span style={{ color: 'yellow' }}>{data.humidity}</span></Typography>
                <Typography> <i class="fas fa-wind"/> WindSpeed : <span style={{ color: 'yellow' }}>{data.windSpeed}</span></Typography>
            <Typography><i class="fas fa-smog"/> Visibility  : <span style={{ color: 'yellow' }}>{data.visibility}</span></Typography>
        </Box>


    </Box>
</Box>

)
function Dashboard({ degUnit, data }) {

    return (
        <Box class='p-4 bg-red-90'>
            <Box class='flex items-center  flex-col sm:flex-row space-y-2 sm:space-y-0 mb-2'>
                <Typography variant='p' className='self-start text-2xl border-2  border-yellow-200
                font-medium  bg-black p-2 rounded-lg' sx={{color:'primary.light'}}>Dashboard</Typography>
                <Alert severity='info' className='mx-auto' >Click on A city to View its Weekly Forecast!</Alert>
            </Box>
            <Grid class='flex justify-start overflow-x-auto space-x-3 mt-4'>
                {data.map(data => <Item data={data} degUnit={degUnit} />)}
            </Grid>
        </Box>
    )
};


export default Dashboard;